* {
  box-sizing: border-box;
}

html {
  font-family: "Roboto", sans-serif;
}

body {
  margin: 0;
}

.header_container {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  width: 100%;
  height: 60px;
  background: #fff;
  box-shadow: 2px 4px rgba(0, 0, 0, 0.05);
}

.nav_container {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  width: 100%;
  height: 60px;
}

.box {
  align-self: center;
  padding: 5px 10px;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  text-transform: uppercase;
  color: #000;
  text-decoration: solid;
}

.logo {
  background-image: url(../images/logo.png);
  width: 40px;
  height: 40px;
  left: 50px;
  top: 10px;
  position: relative;
}

.box:hover {
  color: #00acdc;
}

.is-active:active {
  position: absolute;
  width: 100%;
  height: 4px;
  left: 397px;
  top: 56px;
  background: #00acdc;
  border-radius: 2px;
}

.row {
  display: flex;
  justify-content: space-between;
  margin: 50px;
  flex-grow: 1;
  gap: 48px;
}

.product_card {
  display: flex;
  width: 200px;
  height: 404px;
}

.card {
  width: 200px;
  height: 404px;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 20px 16px;
}

.product_logo {
  width: 160px;
  height: 134px;
  background-image: url(../images/imac.jpeg);
  background-size: cover;
}

.product_name {
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  width: 168px;
  height: 36px;
  padding: 30px 0 ;
  color: #060b35;
}

.product_code {
  font-size: 10px;
  line-height: 14px;
  color: #616070;
}

.review {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.stars {
  display: flex;
  flex-grow: 0;
  margin-top: 16px;
}

.stars__star {
  background-image: url(../images/star.svg);
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  background-position: center;
  flex-grow: 1;
}

.stars--0 .stars__star:nth-child(-n+4) {
  background-image: url(../images/star-active.svg);
}

.reviews {
  display: flex;
  align-items: flex-end;
  width: 53px;
  font-size: 10px;
  line-height: 14px;
  color: #060b35;
  flex-grow: 0;
}

.price_box {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 168px;
  height: 18px;
  margin-top: 24px;
}

.price {
  font-size: 12px;
  line-height: 18px;
  display: flex;
  align-items: flex-end;
  color: #616070;
}

.price_numbers {
  font-weight: bold;
  font-size: 16px;
  line-height: 18px;
  display: flex;
  align-items: flex-end;
  text-align: right;
  color: #060b35;
}

.button {
  margin-top: 16px;
  width: 168px;
  height: 40px;
  bottom: 16px;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
}

.button:hover {
  background: #fff;
  color: #00acdc;
}
